.consultcontainer {
  height: 100%;
  min-height: 100%;
  width: 100%;
}

@media screen and (min-width: 768px) {
  page {
    max-width: 450px;
  }
}

.hide {
  display: none !important;
}

.consultcontent {
  height: 100%;
  overflow: hidden;
}

.chatuibox {
  height: 100%;
  width: 100%;
  max-width: 100%;
  font-family: 黑體-繁, 微軟正黑體, Microsoft JhengHei, sans-serif !important;
  letter-spacing: 1px;
}

.chatpanel {
  height: 100%;
  width: 100%;
}

.chatui {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.chatfriend {
  height: 40px;
  min-height: 40px;
  width: 100%;
  position: relative;
  background-color: #85a1d2;
  /* border-radius: 12px 12px 0 0; */
}

.chatfriendbox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  height: 100%;
}

.icon {
  width: 20px;
  height: 20px;
  user-select: none;
  -webkit-user-drag: none;
  cursor: pointer;
}

.icon:active {
  filter: drop-shadow(0 2px 1px grey);
}

.chatfriendlogo {
  overflow: hidden;
  /* height: 20px;
  width: 15px; */
  width: 20px;
  margin-right: 5px;
  background-color: #fff;
  cursor: pointer;
}

.chatfriendname {
  text-align: left;
  font-size: 16px;
  font-weight: bold;
  height: 20px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  -webkit-user-drag: none;
}

.chatfriendname>img {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  vertical-align: baseline;
}

.chatfriendicon {
  display: flex;
}

.chatfriendseach,
.chatfriendinfo,
.chatfriendmenu {
  height: 20px;
  position: relative;
  margin-left: 10px;
}

.chatfriendseach {
  display: none;
}

.chatfriendinfo>img {
  height: 20px;
}

.chatfriendmenu>img {
  height: 20px;
}

.chatfriendseach>img {
  height: 20px;
}

.chatfriendinfobtn,
.chatfriendmenubtn,
.chatfriendseachbtn {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.chathistorybox {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #85a1d2;
}

.chathistory {
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 0 5px 4px;
  box-sizing: border-box;
  padding-bottom: 20px;
}

/* 聊天记录style */
.uni-list_chat {
  background: none;
  padding-bottom: 50px;
}

.uni-list-item_chat {
  width: 100%;
  box-sizing: border-box;
  background: none;
  border: none;
}

/* end聊天记录 */

.chattoolbox {
  min-height: 42px;
  width: 100%;
  padding: 5px 10px;
  background-color: #fff;
  overflow: hidden;
  height: auto;
  flex: none;
}

.chattoolblock {
  display: block;
  box-sizing: border-box;
}

.chattool {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.chattool embed {
  height: 100%;
  width: 100%;
}

.chattools {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-right: 5px;
  height: 32px;
}

.chattoolright {
  position: relative;
  height: 32px;
  width: 20px;
  min-width: 20px;
  margin-right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chattoolrightbtn {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.chatinputimg,
.chatinputcamera,
.chatinputadd {
  width: 22px;
  min-width: 22px;
  height: 22px;
  margin-right: 10px;
  position: relative;
}

.chatinputmic {
  margin: 0;
  width: 20px;
  min-width: 20px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.chatinputimgbtn,
.chatinputcamerabtn,
.chatinputaddbtn,
.chatinputmicbtn,
.chattoolmicbtn,
.chatinputsubmitbtn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.icon-btn {
  cursor: pointer;
}

.icon-btn:hover>.icon {
  filter: drop-shadow(0 2px 1px grey);
  -webkit-filter: drop-shadow(0 2px 1px grey);
}

.chatinputimg>img {
  width: 100%;
  height: 100%;
}

.chatinputsubmit {
  margin: 0;
  width: 22px;
  min-width: 22px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.chatinputsubmit>img {
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.chatinputbox {
  width: 100%;
  height: auto;
  max-height: 180px;
  border-radius: 15px;
  background-color: #f5f5f5;
  border: 1px solid #e4e4e4;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding: 3px 8px;
  transition: width .3s;
  -webkit-transition: width .3s;
  margin-right: 10px;
  overflow: hidden;
}

.chatinputcontent {
  min-height: 26px;
  width: 100%;
  position: relative;
}

.chatinputcontentlabel {
  position: absolute;
  left: 8px;
  color: gray;
  font-size: 12px;
  height: 26px;
  line-height: 26px;
  text-align: left;
}

.chatinputtxt {
  outline: none;
  padding: 0;
  border: none;
  background-color: inherit;
  font-size: 16px;
  width: 100%;
  height: auto;
  max-height: 180px;
  line-height: 26px;
  min-height: 26px;
  overflow-y: auto !important;
  overflow-x: hidden !important;
}

textarea::-webkit-resizer {
  display: none;
}

textarea::-moz-resizer {
  display: none;
}

textarea::-o-resizer {
  display: none;
}

textarea::-ms-resizer {
  display: none;
}

textarea::resizer {
  display: none;
}

.auto-height {
  height: auto;
}

.placeholder-text {
  color: #999;
  font-size: 12px;
  line-height: 26px;
}

.chatinputtxt::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999;
  font-size: 12px;
  line-height: 26px;
}

.chatinputtxt::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
  font-size: 12px;
  line-height: 26px;
}

.chatinputtxt::-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #999;
  font-size: 12px;
  line-height: 26px;
}

.chatinputemoji {
  position: relative;
  width: 20px;
  min-width: 20px;
  height: 26px;
  overflow: hidden;
}

.chatinputemojibox {
  height: 100%;
  display: flex;
  align-items: center;
}

.chatinputemojibox>img {
  width: 20px;
  height: 20px;
}

.chatinputemojibtn {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.scroll-view {
  /* #ifndef APP-NVUE */
  width: 100%;
  height: 100%;
  /* #endif */
  flex: 1
}

.scroll-view-box {
  flex: 1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.chatDisclaimer {
  width: 100%;
}


.warnMessage {
  color: chocolate;
}

/* card */

.chathistorydate {
  width: 100%;
  text-align: center;
  margin-bottom: 4px;
}

.chatmessagedate {
  font-size: 12px;
  line-height: 12px;
  color: #fff;
  background-color: #00000028;
  padding: 3px 8px;
  border-radius: 13px;
  font-weight: lighter;
}

.chatinfocard {
  margin-bottom: 4px;
  cursor: pointer;
}

.selfchatcard {
  display: flex;
  justify-content: flex-end;
  min-height: 30px;
}

.chatmessagelabe {
  min-width: 60px;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
}

.chatmessagetime,
.chatmessagestate {
  min-width: 60px;
  font-size: 8px;
  line-height: 12px;
  word-break: break-all;
  letter-spacing: 0px;
  color: #3e5474;
  font-weight: bold;
}

.chatmessagestate {
  display: none;
}

.selfchatcard>.chatmessage {
  background-color: #93DF83;
}

.selfchatcard>.chatmessage::before {
  content: '';
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-bottom-color: #93DF83;
  top: -6px;
  right: -3px;
  position: absolute;
  z-index: 0;
  transform: rotate(-60deg);
}

.chatmessage {
  margin: 4px 4px 0;
  position: relative;
  text-align: left;
  max-width: 100%;
  background-color: #fff;
  border-radius: 14px;
  padding: 6px;
  font-size: 14px;
  font-weight: bold;
  color: #111111;
  word-break: break-all;
  white-space: pre-wrap;
}

.friendchatcard {
  display: flex;
  justify-content: flex-start;
  min-height: 30px;
}

.chatavatar {
  height: 30px;
  width: 30px;
  min-width: 30px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 4px;
  background-color: #fff;
}

.chatavatar .chatavatar-img {
  height: 30px;
  width: 30px;
  margin: 0;
  padding: 0;
  border: none;
  vertical-align: middle;
  background-color: #fff;
}

.friendchatcard .chatmessage {
  background-color: #fff;
}

.friendchatcard .chatmessage::before {
  content: '';
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-bottom-color: #fff;
  top: -6px;
  left: -3px;
  position: absolute;
  z-index: 0;
  transform: rotate(60deg);
}

.friendchatcard .chatmessagelabe {
  text-align: left;
}

.selfchatcard .chatmessagelabe {
  text-align: right;
}

.friendchatcard>.chatmessage:active {
  background-color: #ccc;
}

.friendchatcard>.chatmessage:active::before {
  border-bottom-color: #ccc;
}

.selfchatcard>.chatmessage:active {
  background-color: #60ac50;
}

.selfchatcard>.chatmessage:active::before {
  border-bottom-color: #60ac50;
}

.chatmessage.imageMessageCard {
  padding: 0;
}

.chatmessage.imageMessageCard::before {
  content: '';
  display: none;
}

.messageList {
  padding-bottom: 20px;
}

/*  content */

.messagecontent {
  white-space: pre-wrap;
  /* 保留换行符 */
  user-select: auto;
  -webkit-user-select: auto;
  /* PC端  */
  /*火狐*/
  -moz-user-select: text;
  /*chrome浏览器*/
  -webkit-user-select: text;
  /*IE10*/
  -ms-user-select: text;
  /*早期浏览器*/
  -khtml-user-select: text;
  user-select: text;
  /* MB端 */
  -webkit-touch-callout: text;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
  font-family: 黑體-繁, 微軟正黑體, Microsoft JhengHei, sans-serif !important;
  line-height: normal !important;
  font-size: 14px !important;
  font-weight: bold !important;
  color: #111111 !important;
  word-break: break-all !important;
}

.messagecontent a {
  color: blue;
}

.imageMessage {
  width: 100%;
  max-width: 300px;
  height: auto;
  max-height: 400px;
  display: block;
}


.messagePicture {
  overflow: hidden;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.loading-container {
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 10px auto;
}

.loading-text {
  display: block;
  text-align: center;
  font-size: 14px;
  color: #fff;
}

/* 设置位置 */
.loading-box {
  /* 高度 */
  height: 20px;
  /* 弹性布局 */
  display: flex;
  /* 设置子项在y轴方向居中，应该是设置起点在中间，非常有用，不然动画很怪 */
  align-items: center;
}

/* 小竖条 */
.loading-item {
  height: 20px;
  width: 3px;
  background: white;
  /* 加margin，使竖条之间有空隙 */
  margin: 0px 3px;
  /* 圆角 */
  border-radius: 10px;
  /* 动画：名称、时间、循环 */
  animation: loading 1s infinite;
}

/* 设置动画 */
@keyframes loading {
  0% {
    height: 0px;
  }

  50% {
    height: 20px;
  }

  100% {
    height: 0px;
  }
}

/* 为每一个竖条设置延时 */
.loading-item:nth-child(2) {
  animation-delay: 0.1s;
}

.loading-item:nth-child(3) {
  animation-delay: 0.2s;
}

.loading-item:nth-child(4) {
  animation-delay: 0.3s;
}

.loading-item:nth-child(5) {
  animation-delay: 0.4s;
}

.loading-item:nth-child(6) {
  animation-delay: 0.5s;
}

.loading-item:nth-child(7) {
  animation-delay: 0.6s;
}

.loading-item:nth-child(8) {
  animation-delay: 0.7s;
}

.cardtools {
  display: flex;
  flex-wrap: wrap;
  max-width: 200px;
  /* background-color: #333; */
}

.cardtools-btn {
  display: inline-block;
  /* border: grey; */
  /* color: #fff; */
  font-size: 14px;
  padding: 0 2px 2px;
  margin: 0;
  /* background-color: #333; */
  cursor: pointer;
  user-select: none;
  -webkit-user-drag: none;
}

.cardtools-btn:hover {
  /* background-color: #5d5d5d; */
}

.ellipsis {
  white-space: nowrap !important;
